.create {
  position: relative;
  width: 100%;
}

.create header {
  border-bottom: 1px #000 solid;
  font-size: 1.2em;
  height: 20px;
  padding: 20px;
}

.create .steps {
  border-bottom: 1px #000 solid;
  font-size: 1em;
}

.create .steps .detail {
  display: none;
}

.create .steps.step1 .step1,
.create .steps.step2 .step2,
.create .steps.step3 .step3 {
  font-size: 1.3em;
}

.create .steps.step1 .step1 .detail,
.create .steps.step2 .step2 .detail,
.create .steps.step3 .step3 .detail {
  display: block;
}

.create .body {
  padding: 20px;
}

.create ul li {
 padding: 5px 10px;
}

/* Camera */

.create .camera .video {
  background: black;
  box-sizing: border-box;
  display: none;
  left: 0;
  overflow: hidden;
  padding: 20px;
  position: absolute;
  text-align: center;
  top: 0;
  width: 100%;
}

.create .camera.video .video {
  display: block;
}

.create .camera .video .controls {
  bottom: 0;
  height: 50px;
  left: calc(50% - 60px);
  position: absolute;
  width: 120px;
}

.create canvas {
  display: none;
}

.create .body img,
.create .camera .video video {
  width: 100%;
  -moz-transform: scale(-1, 1);
  -webkit-transform: scale(-1, 1);
  -o-transform: scale(-1, 1);
  transform: scale(-1, 1);
  filter: FlipH;
}

/* Frame */

.create .frame.ghost img {
  background: url('https://drewjarrett.github.io/images/temp-notlicenced-ghostbg.png') no-repeat 0 0;
  border: 5px #FFF solid;
}

.create .frame.memory img {
  border: 5px blue solid;
}

.create .frame.memory button.memory,
.create .frame.ghost button.ghost {
  color: grey;
  opacity: 0.7;
}

.create .frame.ghost img {
  opacity: 0.8;
}
